.calc {
    height: 90px;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    display: grid;
    grid-template-columns: repeat(4, 68px);
}

input {
    grid-column: span 4;
    height: 70px;
    width: 260px;
    background-color: whitesmoke;
    border: none;
    border-radius: 30px;
    font-size: 50px;
    text-align: end;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 20px;

}

button {
    height: 48px;
    width: 48px;
    border: none;
    border-radius: 40%;
    padding: 20px;
    font-size: 15px;
    margin: 8px;
    background-color: skyblue;

}

.btn {
    background-color: bisque;
}

h1 {
    text-align: center;

}

#cal {
    text-align: center;
}